/*! @group Layout */
/* ========================================================================== */
.wrapper {
	width: 100%;
	height: 100vh;
	display: block;
	//align-items: center;
	//justify-content: center;
	position: relative;
	//background-color: $third;
	background: url("../images/cp_beginBack.jpg") 50% 0 no-repeat;
	background-size: auto 100%;
	overflow: hidden;
	//transition: all .3s;
	//animation: fadeIn 0.6s ease-in-out;
	z-index: 1;

	&__intro {
		width: 100%;
		height: 100vh;
		display: block;
		position: absolute;
		left: 0;
		top: 0;
		background: url("../images/cp_beginBack.jpg") 50% 0 no-repeat;
		background-size: auto 100%;
		z-index: 0;
		opacity: 0;
	}

	&__game {
		width: 100%;
		height: 100vh;
		display: block;
		position: absolute;
		left: 0;
		top: 0;
		background: url("../images/cp_gameBack.jpg") 50% 0 no-repeat;
		background-size: auto 100%;
		z-index: 0;

		&_arrow {
			width: 6vmin;
			height: auto;
			display: block;
			position: absolute;
			left: 0;
			bottom: -11vh;

			img {
				width: 100%;
				height: 100%;
				display: block;
				object-position: center;
				object-fit: cover;
			}
		}

		&_clear,
		&_next {
			width: 6vmin;
			height: auto;
			display: block;
			position: absolute;
			right: 0;
			bottom: -11vh;

			img {
				width: 100%;
				height: 100%;
				display: block;
				object-position: center;
				object-fit: cover;
			}
		}
	}

	&__top {
		//width: calc(#{$introTopBlockHeight} * 2.8);
		//height: $introTopBlockHeight;
		width: $introTopBlockWidth;
		height: calc(#{$introTopBlockWidth} / 2.8);
		display: flex;
		flex-direction: row;
		justify-content: center;
		position: fixed;
		top: 0;
		left: 50%;
		transform: translateX(-50%);
		//margin: 0 calc((100% - #{$introTopBlockWidth}) / 2);
		z-index: 2;
		opacity: 0;
	}

	&__title {
		width: $introTitleBlockWidth;
		//height: $introTitleBlockHeight;
		display: flex;
		flex-direction: column;
		position: fixed;
		top: calc((#{$introTopBlockWidth} / 2.8) - ((#{$introTopBlockWidth} / 2.8) / 4));
		left: 0;
		margin: 1rem calc((100% - #{$introTitleBlockWidth}) / 2) 0;
		z-index: 2;
		opacity: 0;

		&_top, &_bottom {
			display: flex;
			flex-direction: row;
			justify-content: center;
		}

		&_symbol {
			//width: $introTitleSymbolWidth;
			width: auto;
			height: 10vw;
			display: block;
			position: relative;

			img {
				@extend %imgCenter;
			}
		}
	}

	&__bottom {
		width: calc(#{$introTitleBlockWidth} / 1.3);
		display: block;
		position: absolute;
		left: 0;
		bottom: 0;
		margin: 0 calc((100% - (#{$introTitleBlockWidth} / 1.3)) / 2) 0;
		z-index: 3;
		opacity: 0;

		&_menu {
			width: 100%;
			list-style: none;
			display: flex;
			flex-direction: row;
			justify-content: space-between;
			margin: 0 0 6vmax;

			li {
				width: calc(100% / 3.5);
				height: 100%;
				display: block;
				position: relative;

				a {
					width: calc(100% - 2rem);
					height: 100%;
					display: flex;
					align-items: center;
					justify-content: center;
					padding: 0.5rem 1rem;
					background: linear-gradient(lighten($five, 35%), $five);
					border-radius: 0.3rem;
					color: $secondary;
					font-size: $font-size-h5;
					font-family: $heading-font;
				}

				&:hover {

					a {
						color: lighten($secondary, 15%);
						background: linear-gradient(lighten($five, 45%), lighten($five, 10%));
					}
				}
			}
		}
	}

	&__stars {
		height: 3rem;
		display: flex;
		flex-direction: row;
		list-style: none;
		position: absolute;
		top: 0;
		left: 50%;
		margin: 1rem 0 0;
		transform: translateX(-50%);

		li {
			width: 5vmin;
			height: 5vmin;
			margin-right: 0.5rem;
			background-image: url("../images/cp_stars_empty.png");
			background-position: 0 0;
			background-size: auto 100%;

			&.fill {
				background-image: url("../images/cp_stars_fill.png");
			}

			picture {
				width: 100%;
				height: 100%;
				display: block;

				img {
					width: 100%;
					height: 100%;
					object-position: center;
					object-fit: cover;
				}
			}

			&:last-child {
				margin-right: 0;
			}
		}
	}

	//@include grid-media($Medium) {
	//	//padding: 6rem 1rem 0;
	//}
	//
	//@include grid-media($XSmall) {
	//
	//	&_news {
	//		padding-right: 0;
	//	}
	//}

	@include grid-media($Small) {
		//height: inherit;
		//position: fixed;
		//min-height: 100vh;

		&__top {
			width: $introTopBlockWidthMobile;
			height: calc(#{$introTopBlockWidthMobile} / 2.8);
		}

		&__title {
			width: $introTopBlockWidthMobile;
			margin: 3rem calc((100% - #{$introTopBlockWidthMobile}) / 2) 0;

			&_symbol {
				height: 12vw;
			}
		}

		&__bottom {
			width: calc(100% - 2rem);
			margin: 0 1rem;
			bottom: inherit;
			top: 65vmin;

			&_menu {
				width: 100%;
				flex-direction: column;
				justify-content: normal;
				align-items: flex-start;

				li {
					width: 100%;
					margin-bottom: 1.5rem;

					a {
						padding: 1rem;
					}
				}
			}
		}

		&__game {

			&_arrow {
				width: 10vmin;
				left: 1rem;
				bottom: 2.5vh;
			}

			&_clear,
			&_next {
				width: 10vmin;
				right: 1rem;
				bottom: 2.5vh;
			}
		}
	}
}

.container {
	width: calc(#{$containerHeight} * 1.4);
	height: $containerHeight;
	display: flex;
	align-items: center;
	position: absolute;
	top: 50%;
	left: 50%;
	margin-top: -3.5vh;
	transform: translate(-50%, -50%);
	//background-color: cornflowerblue;
	z-index: 1;

	&__wrapper {
		width: 100%;
		height: 100%;
		display: flex;
		align-items: center;
		justify-content: center;
		position: relative;

		//&_padding {
		//	width: calc(100% - (#{$containerPadding} * 2));
		//	height: calc(100% - (#{$containerPadding} * 2));
		//	padding: $containerPadding;
		//}

		&_category {
			width: 100%;
			display: flex;
			flex-direction: column-reverse;
			justify-content: space-between;
			align-items: center;
		}
	}

	&__category {
		width: 100%;
		display: flex;
		flex-direction: row;
	}

	&__title {
		height: auto;
		display: block;
		position: relative;
	}

	&__inside {
		width: $containerInsideWidth;
		height: inherit;
		display: block;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		z-index: 2;

		&_back {
			width: 100%;
			//height: 100%;
			display: block;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			z-index: 1;

			img {
				width: 100%;
				height: 100%;
				display: block;
				object-position: center;
				object-fit: cover;
			}
		}
	}

	&__back {
		width: 100%;
		height: 100%;
		display: block;
		z-index: 1;

		img {
			width: 100%;
			height: 100%;
			display: block;
			object-position: center;
			object-fit: cover;
		}
	}

	&__frame {
		width: $frameWidth;
		height: $frameHeight;
		display: block;
		position: absolute;
		z-index: 5;
		top: 50%;
		transform: translate(-50%, -50%);

		&_left {
			left: $frameLeft;
		}

		&_right {
			left: $frameRight;
		}

		.frame {
			&__left {

				&_top {
					width: 100%;
					height: auto;
					display: block;
					position: absolute;
					top: 50%;
					left: 50%;
					transform: translate(-50%, -50%);
					z-index: 2;

					img {
						width: 100%;
						height: 100%;
						display: block;
						object-position: center;
						object-fit: cover;
					}
				}

				&_back {
					width: $frameBackWidth;
					height: auto;
					display: block;
					position: absolute;
					top: 50%;
					left: 50%;
					transform: translate(-50%, -($frameBackLeftTop));
					z-index: 1;

					img {
						width: 100%;
						height: 100%;
						display: block;
						object-position: center;
						object-fit: cover;
					}
				}

				&_title {
					width: 100%;
					height: auto;
					display: block;
					position: absolute;
					top: 50%;
					left: 50%;
					text-align: center;
					line-height: 1;
					margin-top: -57.3%;
					z-index: 5;
					transform: translate(-50%, -50%);

					h2 {
						width: 100%;
						height: auto;
						display: block;
						position: absolute;
						margin: 0;
						top: 50%;
						left: 50%;
						transform: translate(-50%, -50%);
						color: $primary;
					}
				}

				&_quest {
					width: 80%;
					height: auto;
					display: block;
					position: absolute;
					top: 50%;
					left: 50%;
					text-align: center;
					line-height: 1;
					margin-top: -38%;
					z-index: 5;
					transform: translate(-50%, -50%);

					img {
						width: 100%;
						height: 100%;
						display: block;
						object-position: center;
						object-fit: cover;
						position: relative;
					}

					span {
						width: calc(100% - 2.6rem);
						height: auto;
						display: block;
						position: absolute;
						padding-left: 1.3rem;
						padding-right: 1.3rem;
						top: 50%;
						left: 50%;
						transform: translate(-50%, -50%);
						z-index: 2;
					}
				}

				&_text {
					width: 70%;
					height: 32vh;
					display: block;
					position: absolute;
					top: 50%;
					left: 50%;
					margin-top: 1rem;
					transform: translate(-50%, -50%);
					z-index: 5;

					p {
						width: 100%;
						height: auto;
						display: block;
						position: absolute;
						margin: 0;
						top: 50%;
						left: 50%;
						transform: translate(-50%, -50%);
					}

					ul {

						li {
							font-size: $font-size-small;
						}
					}
				}

				&_wrong {
					width: 80%;
					height: auto;
					display: block;
					position: absolute;
					top: 50%;
					left: 50%;
					text-align: center;
					line-height: 1;
					margin-top: 35%;
					z-index: 5;
					transform: translate(-50%, -50%);
					opacity: 0;

					img {
						width: 100%;
						height: 100%;
						display: block;
						object-position: center;
						object-fit: cover;
						position: relative;
					}

					span {
						width: calc(100% - 2.6rem);
						height: auto;
						display: block;
						position: absolute;
						padding-left: 1.3rem;
						padding-right: 1.3rem;
						top: 50%;
						left: 50%;
						margin-top: 4%;
						transform: translate(-50%, -50%);
						font-family: $heading-font;
						font-size: $font-size-h3;
						font-weight: 500;
						z-index: 2;
					}
				}

				@include grid-media($Medium) {

					&_title {
						margin-top: -56.7%;
					}
				}
			}

			&__right {

				&_top {
					width: 100%;
					height: auto;
					display: block;
					position: absolute;
					top: 50%;
					left: 50%;
					transform: translate(-50%, -50%);
					z-index: 2;

					img {
						width: 100%;
						height: 100%;
						display: block;
						object-position: center;
						object-fit: cover;
					}
				}

				&_back {
					width: $frameBackWidth;
					height: auto;
					display: block;
					position: absolute;
					top: 50%;
					left: 50%;
					transform: translate(-50%, -($frameBackRightTop));
					z-index: 1;

					img {
						width: 100%;
						height: 100%;
						display: block;
						object-position: center;
						object-fit: cover;
					}
				}

				&_text {
					width: 70%;
					//height: 40vh;
					display: block;
					position: absolute;
					top: 50%;
					left: 50%;
					transform: translate(-50%, -50%);
					z-index: 5;
					//text-align: justify;

					p {
						width: 100%;
						height: auto;
						display: block;
						position: relative;
						margin: 0;
						//top: 50%;
						//left: 50%;
						//transform: translate(-50%, -50%);
					}

					ul {
						width: 100%;
						height: auto;
						display: block;
						position: relative;
					}
				}

				&_buttons {
					width: 75%;
					height: auto;
					display: block;
					position: absolute;
					top: 50%;
					left: 50%;
					transform: translate(-50%, -($frameBackRightTop));
					z-index: 5;
				}

				&_button {
					width: 100%;
					height: calc(100% / 3);
					display: block;
					position: relative;
					margin: 15% 0;
					z-index: 1;
					cursor: pointer;

					img {
						width: 100%;
						height: 100%;
						display: block;
						object-position: center;
						object-fit: cover;
						transition: transform ($base-duration - 0.1);
					}

					p {
						width: calc(100% - 6rem);
						height: auto;
						display: block;
						position: absolute;
						padding-left: 3rem;
						padding-right: 3rem;
						margin: 0;
						top: 50%;
						left: 50%;
						transform: translate(-50%, -50%);
						text-align: center;
						line-height: 1;
						z-index: 2;
					}

					&:hover {
						img {
							transform: scale(0.99);
						}
					}
				}
			}
		}
	}

	&__arrow {
		width: 4rem;
		height: auto;
		display: block;
		position: absolute;
		cursor: pointer;

		&_back {
			bottom: 7%;
			left: -7%;
		}

		&_next {
			bottom: 7%;
			right: -7%;
		}

		img {
			width: 100%;
			height: 100%;
			display: block;
			position: relative;
			object-position: center;
			object-fit: cover;
		}
	}

	&__about {
		width: 100%;
		height: 100vh;
		display: block;
		position: absolute;
		top: 0;
		left: 0;
		background: url("../images/woodBack.jpg") 0 0 repeat;
		overflow: hidden;
		z-index: 11;

		&_inside {
			width: $containerWidth;
			height: calc(#{$containerWidth} / 1.57);
			display: block;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}

		&_back {
			width: 100%;
			height: 100%;
			display: block;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			z-index: 1;

			img {
				width: 100%;
				height: 100%;
				display: block;
				object-position: center;
				object-fit: cover;
			}
		}

		&_frame {
			width: auto;
			height: 100%;
			display: block;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			z-index: 3;

			img {
				width: 100%;
				height: 100%;
				display: block;
				object-position: center;
				object-fit: cover;
			}

			&back {
				width: 40%;
				height: 90%;
				display: block;
				position: absolute;
				top: 50%;
				left: 50%;
				transform: translate(-50%, -50%);
				z-index: 2;

				img {
					width: 100%;
					height: 100%;
					display: block;
					object-position: center;
					object-fit: cover;
				}
			}
		}

		&_text {
			width: calc(38% - 2rem);
			height: calc(87% - 2rem);
			display: block;
			position: absolute;
			top: 50%;
			left: 50%;
			padding: 1rem;
			text-align: center;
			transform: translate(-50%, -50%);
			z-index: 2;
		}

		&_title {
			color: $primary;
			//margin-top: 0.3rem;
		}

		&_person {
			margin-bottom: 0.3rem;

			h3 {
				margin: 0 0 0.3rem 0;
				color: $primary;
			}

			p {
				margin: 0;
				font-size: $font-size-large;
			}

			&:last-child {
				.container__about_line {
					display: none;
					visibility: hidden;
				}
			}
		}

		&_line {
			width: 100%;
			height: auto;
			display: block;
			position: relative;

			img {
				width: 100%;
				height: 100%;
				display: block;
				object-position: center;
				object-fit: cover;
			}
		}

		&_arrow {
			width: auto;
			height: 5vmin;
			display: block;
			position: absolute;
			bottom: 6vh;
			left: 50%;
			transform: translateX(-50%);
			cursor: pointer;

			img {
				width: 100%;
				height: 100%;
				display: block;
				position: relative;
				object-position: center;
				object-fit: cover;
			}
		}

		//@include grid-media($Large) {
		//
		//	&_inside {
		//		width: calc(#{$containerWidth} + (#{$containerWidth} / 3));
		//		height: calc((#{$containerWidth} + (#{$containerWidth} / 3)) / 1.57);
		//	}
		//}

		@include grid-media($XXLarge) {
			&_person {
				p {
					font-size: $font-size-large;
				}
			}
		}

		@include grid-media($XLarge) {
			&_person {
				p {
					font-size: $font-size-large;
				}
			}
		}

		@include grid-media($Large) {
			&_person {
				p {
					font-size: $font-size-large;
				}
			}
		}

		@include grid-media($Medium) {

			&_inside {
				width: $containerWidth;
				height: calc(#{$containerWidth} / 1.57);
			}

			&_person {
				p {
					font-size: $font-size-large;
				}
			}

			&_title {
				margin-top: 0.3rem;
			}
		}

		@include grid-media($HSmall) {

			&_inside {
				width: $containerWidth;
				height: calc(#{$containerWidth} / 1.57);
			}

			&_person {
				p {
					font-size: $font-size-large;
				}
			}

			&_title {
				margin-top: 0.3rem;
			}
		}

		@include grid-media($Small) {
			width: calc(100% - 2rem);
			height: inherit;
			min-height: calc(100vh - 3rem);
			padding: 3rem 1rem 0;
			position: fixed;
			top: 0;
			left: 0;

			&_inside {
				width: $containerAboutWidth;
				//height: inherit;
				//min-height: 80vh;
				display: block;
				//position: relative;
				//left: 0;
				//top: 0;
				//transform: translate(0, 0);
			}

			&_back {
				display: none;
				visibility: hidden;
			}

			&_frame {
				width: 100%;
				height: auto;

				&back {
					width: 85%;
					height: 70%;
				}
			}

			&_text {
				width: calc(85% - 2rem);
				height: calc(70% - 2rem);
			}

			&_arrow {
				height: 10vmin;
				//position: relative;
				top: 2vh;

				&_back {
					bottom: 6rem;
					left: 0;
				}

				&_next {
					bottom: 6rem;
					right: 0;
				}
			}
		}
	}

	&__info {
		width: 100%;
		height: 100vh;
		display: block;
		position: absolute;
		top: 0;
		left: 0;
		background: url("../images/woodBack.jpg") 0 0 repeat;
		overflow: hidden;
		z-index: 11;

		&_inside {
			width: $containerWidth;
			height: calc(#{$containerWidth} / 1.57);
			display: block;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
		}

		&_frame {
			width: 100%;
			height: auto;
			display: block;
			position: absolute;
			top: 50%;
			left: 50%;
			margin-top: -2rem;
			transform: translate(-50%, -50%);
			z-index: 3;

			img {
				width: 100%;
				height: 100%;
				display: block;
				object-position: center;
				object-fit: cover;
			}
		}

		&_back {
			width: 90%;
			height: auto;
			display: block;
			position: absolute;
			top: 50%;
			left: 50%;
			//margin-top: 2rem;
